<template>
  <div class="loop">
    <div class="top">
      <div class="el">
        <el-select v-model="value1" placeholder="病毒类型">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>

          <el-date-picker
                v-model="value2"
                type="daterange"
                :editable='false'
                start-placeholder="开始日期"
                end-placeholder="结束日期">
              </el-date-picker>
      </div>


      <div class="dao" @click='select'>
        <span></span>
        查询
      </div>


    </div>

    <div class="tiao">
      全部
    </div>
    <div class="table">

      <el-table
            :data="tableData.dataList"
            style="width: 100%">
            <el-table-column
                  type="index"
                   label="序号"
                  width="50">
                </el-table-column>
            <el-table-column
              prop="virusIp"
              label="被传染单位">
            </el-table-column>
            <el-table-column
              prop="virusName"
              label="病毒名称">
            </el-table-column>
            <el-table-column
              prop="level"
              label="病毒告警级别">
            </el-table-column>
            <el-table-column
              prop="virusType"
              label="病毒类型">
            </el-table-column>
            <el-table-column
              prop="virusSource"
              label="病毒来源">
            </el-table-column>
            <el-table-column
              prop="handleResult"
              label="处理结果">
            </el-table-column>
            <el-table-column
              prop="idt"
              label="时间">
            </el-table-column>
          </el-table>
    </div>

    <div class="page-btn">
      <el-pagination
        background
        layout="prev, pager, next"
        :total='tableData.rowCount'
        :current-page='curPage'
        :page-size='pagesize'
        @current-change='changepage'>
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import {yjAttackGrid,dicMap} from '../../api/virus/virus.js'
  export default{
    mounted(){
      let _this = this;
      (function($){

              yjAttackGrid({
                page: _this.curPage,
                rows: _this.pagesize
              }).then(res => {
                console.log('病毒管理',res);
                _this.tableData = res.result;
              })

              dicMap('VirusPlatform').then(res => {
                console.log('病毒类型',res)
              })




      		    $.fn.placeholder = function(options){
      		        var opts = $.extend({}, $.fn.placeholder.defaults, options);
      		        var isIE = document.all ? true : false;
      		        return this.each(function(){
      		            var _this = this,
      		                placeholderValue =_this.getAttribute("placeholder"); //缓存默认的placeholder值
      		            if(isIE){
      		                _this.setAttribute("value",placeholderValue);
      		                _this.onfocus = function(){
      		                    $.trim(_this.value) == placeholderValue ? _this.value = "" : '';
      		                };
      		                _this.onblur = function(){
      		                    $.trim(_this.value) == "" ? _this.value = placeholderValue : '';
      		                };
      		            }
      		        });
      		    };
      		})(jQuery);
      		$("input").placeholder();
    },
    methods:{
      changepage(ev){
        this.curPage = ev;
        yjAttackGrid({
          page: this.curPage,
          rows: this.pagesize
        }).then(res => {
          console.log('病毒管理',res);
          this.tableData = res.result;
        })
      },
      select(){
        yjAttackGrid({
          virusType: this.value1,
          eventTimeBegin: this.getTime(this.value2[0]),
          eventTimeEnd: this.getTime(this.value2[1])
        }).then(res => {
          console.log('病毒管理',res);
          this.tableData = res.result;
        })
      },
      getTime(date){
        if(!date) return;
        var YY = date.getFullYear() + '-';
        var MM = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
        var DD = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate());
        return YY + MM + DD;
      },
    },
    data(){
      return{
        options: [{
          value: '文档病毒',
          label: '文档病毒'
        }, {
          value: '感染型病毒',
          label: '感染型病毒'
        }, {
          value: '系统异常',
          label: '系统异常'
        }, {
          value: '蠕虫病毒',
          label: '蠕虫病毒'
        }, {
          value: '后门病毒',
          label: '后门病毒'
        }, {
          value: '流氓软件',
          label: '流氓软件'
        }, {
          value: '漏洞利用病毒',
          label: '漏洞利用病毒'
        }, {
          value: '木马病毒',
          label: '木马病毒'
        }, {
          value: '黑客工具',
          label: '黑客工具'
        }],
        curPage: 1,//当前页数
        pagesize: 10,//每页显示条数
        value: '',
        value1: '',
        value2: '',
        tableData: []
      }
    }
  }
</script>

<style lang='less' scoped>
  .right .top{
    position: relative;
    width: 100%;
    .el{
      display: inline-block;
      >div{
        margin: 0 10px;
      }
    }

    .dao{
      position: absolute;
      top: 20px;
      right: 10px;
      width: 85px;
      height: 30px;
      color: #fff;
      background: #5296D1;
      text-align: center;
      line-height: 30px;
      font-size: 14px;
      border-radius: 10px;
      span{
        display: inline-block;
        width: 15px;
        height: 15px;
        position: relative;
        top: 2px;
        background: url(../../assets/imgs/hole/tubiao.png);
        background-size: 100% 100%;
       }
    }
  }

</style>
<style lang='less'>
  .el-select,.el-input{
        width: 108px !important;
        height: 30px;
      }
      .el-date-editor{
        width: 265px !important;
      }
      .el-input__inner {
        height: 30px;
        background-color: #0B5CB9 !important;
        border: none;
        border-radius: 20px;
        font-size: 10px;
        color: #fff;
      }
      .el-input__icon{
        line-height: 30px;
      }
      .el-table th, .el-table tr{
        background: transparent;
      }
      .el-table, .el-table__expanded-cell{
        background: transparent;
      }
      .el-table td, .el-table th.is-leaf{
        border: none;
      }
      .el-table::before {
      	height: 0px;
      }
      .el-table tbody tr:hover>td {
          background-color:transparent!important
      }


      .el-table__body-wrapper .el-table__row{
        td:nth-child(1),td:nth-child(2),td:nth-child(3),td:nth-child(4){
          color: #8BAFBA;
        }

        td:nth-child(5){
          color: #E85259;
        }
        td:nth-child(6){
          color: #6794DD;
        }
        td:nth-child(7),td:nth-child(8),td:nth-child(9){
          color: #fff;
        }
        td:nth-child(10){
          color: #F4B890;
        }
      }



      .el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev, .el-pagination.is-background .el-pager li{
        background: transparent;
        border: 1px solid #fff;
        color: #93D7F5;
        font-weight: 400;
      }

      .el-pagination.is-background .el-pager li.active{
        border-color: transparent;
      }

  .page-btn{
            position: absolute;
            bottom: 50px;
            right: 30px;
          }
</style>
<style>
  .el-table .cell{
    text-align: center !important;
  }
  .el-range-editor .el-range-input{
    background: #0B5CB9;
  }
  .el-date-editor,.el-range-input{
    color: #fff !important;
  }
  .el-date-editor .el-range__icon{
    line-height: 25px;
  }
  .el-input__icon .el-range__close-icon{
    line-height: 25px;
  }
  .el-date-editor .el-range-separator{
    line-height: 25px;
    color: #fff;
  }
</style>
